                                </div>
                            </div>
                            <div class="row">
                                <div class="d-flex">
                                    <h3 class="m-b-0 text-white">{{ numbertradesPend }}</h3>
                                    <i data-feather="chevron-right" class="text-white ml-auto" style="margin-top:6px"></i>
                                </div>
                            </div>
                        </div>
                    </div>
                </a>
            </div>
            <div class="col">
                <a href="javascript:void(0)" @click="tradeInfAdi">
                    <div class="card bg-primary tarjet">
                        <div class="card-body py-3">
                            <div class="row">
                                <div class="d-flex">
                                    <i data-feather="file" class="text-white"></i>
                                    <p class="text-white fw-bold ml-1" style="font-size: 12px; margin-top: 3px;">Doc. derivados
                                    para inf.</p>
                                </div>
                            </div>
                            <div class="row">
                                <div class="d-flex -3">
                                    <h3 class="m-b-0 text-white">{{ numbertradesInfAdi }}</h3>
                                    <i data-feather="chevron-right" class="text-white ml-auto" style="margin-top:6px"></i>
                                </div>
                            </div>
                        </div>
                    </div>
                </a>
            </div>
            <div class="col">
                <a href="javascript:void(0)" @click="tradeInfAdi">
                    <div class="card bg-primary tarjet">
                        <div class="card-body py-3">
                            <div class="row">
                                <div class="d-flex">
                                    <i data-feather="file" class="text-white"></i>
                                    <p class="text-white fw-bold ml-1" style="font-size: 12px; margin-top: 3px;">Documentos atendidos</p>
                                </div>
                            </div>
                            <div class="row">
                                <div class="d-flex -3">
                                    <h3 class="m-b-0 text-white">{{numbertradesFin}}</h3>
                                    <i data-feather="chevron-right" class="text-white ml-auto" style="margin-top:6px"></i>
                                </div>
                            </div>
                        </div>
                    </div>
                </a>
            </div>
            <div class="col">
                <a href="javascript:void(0)" @click="tradeRevInmediata">
                    <div class="card bg-danger tarjet">
                        <div class="card-body py-3">
                            <div class="row">
                                <div class="d-flex">
                                    <i data-feather="file" class="text-white"></i>
                                    <p class="text-white fw-bold ml-1" style="font-size: 12px; margin-top: 3px;">Documentos
                                    pendientes</p>
                                </div>
                            </div>
                            <div class="row">
                                <div class="d-flex">
                                    <h3 class="m-b-0 text-white">{{ numbertradesTradios }}</h3>
                                    <i data-feather="chevron-right" class="text-white ml-auto" style="margin-top:6px"></i>
                                </div>
                            </div>
                        </div>
                    </div>
                </a>
            </div>
        </div>
    </section>
</template>
<script>
import feather from 'feather-icons';
import { getData, postData } from '@/utils/fetchData';
import axios from 'axios';
export default {
    name: 'CardPanelAdmin',

    created() {
        this.listTrades();
    },

    mounted() {
        this.$nextTick(() => {
            feather.replace();
        });
    },

    data() {
        return {
            trades: [],
            numbertradesPend: 0,
            numbertradesInfAdi: 0,
            numbertradesFin: 0,
            numbertradesTradios: 0,
        }
    },

    methods: {
        async listTrades() {
            try {
                const result = await getData('trade', this);
                this.trades = result.data;
                const tradesPend = this.trades.filter(trade => trade.tra_state_law === 'P');
                this.numbertradesPend = tradesPend.length;
                const tradesFin = this.trades.filter(trade => trade.tra_state_law === 'F');
                this.numbertradesFin = tradesFin.length;
                const tradesInfAdi = this.trades.filter(trade => trade.tra_state_mp === 'V');
                this.numbertradesInfAdi = tradesInfAdi.length;

                //Trades Tardes
                // Obtener la fecha actual
                const currentDate = new Date();

                // Filtrar trades que tienen una diferencia de más de 15 días
                 const tradesTardios = this.trades.filter(trade => {
                    const arrivalDate = new Date(trade.tra_arrival_date);
                    const differenceInDays = Math.floor((currentDate - arrivalDate) / (1000 * 60 * 60 * 24));
                    return differenceInDays > 15;
                });

                this.numbertradesTradios = tradesTardios.length;

            } catch (error) {
                this.$handleApiError(error, 'listar trades');
            }
        },

        tradeTotal() {
            this.$router.push({ name: 'trade/lista', params: { tipo_tra: '' } });
        },
        tradePendiente() {
            this.$router.push({ name: 'trade/lista', params: { tipo_tra: 'pendiente' } });
        },
        tradeInfAdi() {
            this.$router.push({ name: 'trade/lista', params: { tipo_tra: 'infoAdi' } });
        },
        tradeTerminado() {
            this.$router.push({ name: 'trade/lista', params: { tipo_tra: 'terminado' } });
        },
        tradeRevInmediata() {
            this.$router.push({ name: 'trade/lista', params: { tipo_tra: 'revInmediata' } });
        },
    }


}

</script>
